.wrapper {
    background-color: #3c313d;
    padding: 1rem;
    margin: 2rem auto;
    width: 50rem;
    max-width: 95%;
    border-radius: 15px;
}

form {
    width: 100%;
    display: flex;
    align-items: center;
}

.form_box {
    width: 100%;
}

.form__control {
    display: flex;
    align-items: center;
    width: 100%;
}

.form__control label {
    min-width: 100px;
    margin-right: 1em;
    color: #ffffff;
}

.form__control input {
    width: 50%;
    padding: 10px 20px;
    border-radius: 20px;
    border: 0;
}

button {
    min-width: 110px;
    margin-left: 10px;
    padding: 10px 20px;
    background: #30D67A;
    border-radius: 20px;
    border: 2px solid transparent;
    text-transform: uppercase;
}

button:hover {
    cursor: pointer;
}

.form__actions button:hover {
    cursor: pointer;
    border: 2px solid #F2CC33;
}
